@import "https://fonts.googleapis.com/css?family=Cherry+Swash";
*, :before, :after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  text-size-adjust: none;
  border: 0;
  margin: 0;
  padding: 0;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

a {
  text-decoration: none;
}

body {
  height: 100vh;
  padding-top: 10vh;
  font-family: sans-serif;
  overflow: hidden;
}

header {
  height: 10vh;
  width: 100%;
  background-color: #34495e;
  justify-content: center;
  align-items: center;
  padding-right: 5%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.logo {
  height: 100%;
  width: 10%;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 767.5px) {
  .logo {
    width: 30%;
  }
}

.logo img {
  width: 40px;
}

@media screen and (min-width: 767.5px) {
  .logo img {
    width: 90px;
  }
}

nav {
  width: 590px;
  height: 50px;
  background-color: #4864819c;
  border-radius: 8px;
  margin: auto;
  font-size: 0;
  display: flex;
  position: relative;
}

nav a {
  z-index: 1;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  display: flex;
  position: relative;
}

nav .animation {
  height: 100%;
  z-index: 0;
  border-radius: 8px;
  transition: all .5s;
  position: absolute;
  top: 0;
}

a:first-child {
  width: 100px;
}

a:nth-child(2) {
  width: 110px;
}

a:nth-child(3) {
  width: 100px;
}

a:nth-child(4) {
  width: 160px;
}

a:nth-child(5) {
  width: 120px;
}

.home__nav .active__nav, a:first-child:hover ~ .animation {
  width: 100px;
  background-color: #1abc9c;
  left: 0;
}

.about__nav .active__nav, a:nth-child(2):hover ~ .animation {
  width: 110px;
  background-color: #e74c3c;
  left: 100px;
}

.blog__nav .active__nav, a:nth-child(3):hover ~ .animation {
  width: 100px;
  background-color: #3498db;
  left: 210px;
}

.portfolio__nav .active__nav, a:nth-child(4):hover ~ .animation {
  width: 160px;
  background-color: #9b59b6;
  left: 310px;
}

.contacts__nav .active__nav, a:nth-child(5):hover ~ .animation {
  width: 120px;
  background-color: #e67e22;
  left: 470px;
}

.home__nav > .active__nav {
  background-color: red;
}

.contacts__body {
  background-image: url("b.cd93ef5d.jpg");
  background-size: 20%;
  overflow: hidden;
}

.contacts__box {
  width: 80%;
  height: 60vh;
  -webkit-backdrop-filter: blur(2px);
  backdrop-filter: blur(2px);
  border-radius: 30px;
  margin: 15vh auto 0;
  overflow: hidden;
}

@media screen and (min-width: 767.5px) {
  .contacts__box {
    display: flex;
  }
}

.contacts__box > ul {
  width: 50%;
  display: flex;
}

.m__t {
  background-color: #f0f8ff97;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.m__t p {
  color: #34495e;
  font-weight: bold;
}

.m__t li {
  width: 280px;
  height: 40px;
  cursor: pointer;
  background-color: #ffffffbc;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  transition: all .5s;
  display: flex;
}

.m__t li:hover {
  background-color: #e67e22;
}

.messengers li:hover svg, .m__t li:hover p, .m__t li:hover svg {
  fill: #fff;
  color: #fff;
}

.m__t a {
  width: 100%;
}

.contacts__box svg {
  width: 30px;
  height: 30px;
  fill: #34495e;
  margin: 0 15px;
}

.messengers {
  background-color: #e67d223e;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.messengers li {
  width: 100%;
  width: 100px;
  height: 100px;
  border: 3px solid #34495e;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin: 20px;
  transition: all .5s;
  display: flex;
}

.messengers li:hover {
  background-color: #e67e22;
  border: 3px solid #fff;
}

.messengers svg {
  width: 50px;
  height: 50px;
  margin: 20px;
}

.about__body {
  background-image: linear-gradient(#34495e, #fff);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 767.5px) {
  .about__body {
    flex-direction: row;
  }
}

.a1 img, .a2 img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
  display: block;
}

@media screen and (min-width: 767.5px) {
  .a1 img, .a2 img {
    width: 100px;
    height: 100px;
  }
}

.a1, .a2 {
  width: 300px;
  height: 300px;
  background-color: #ffffff4a;
  border-radius: 20px;
  margin: 30px;
}

@media screen and (min-width: 767.5px) {
  .a1, .a2 {
    width: 400px;
    height: 400px;
  }
}

.a1 > div, .a2 > div {
  height: 33.33%;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.about__body p {
  width: 200px;
  font-size: 14px;
}

.overlay__box {
  width: 70px;
  height: 70px;
  border: 1px solid #e74c3c;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 767.5px) {
  .overlay__box {
    width: 100px;
    height: 100px;
  }
}

.overlay p {
  font-size: 10px;
}

@media screen and (min-width: 767.5px) {
  .overlay p {
    font-size: 20px;
  }
}

.overlay {
  color: #fff;
  text-align: center;
  width: 70px;
  height: 35px;
  background-color: #e74c3c;
  align-items: center;
  transition: transform 1s linear;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(200%);
}

@media screen and (min-width: 767.5px) {
  .overlay {
    width: 100px;
    height: 50px;
  }
}

.overlay__box:hover .overlay {
  transform: translateY(100%);
}

.title_box {
  height: 80%;
  border: 20px solid #1abc9c;
  border-radius: 50px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10vh;
  display: flex;
  overflow: hidden;
}

@media screen and (min-width: 900.5px) {
  .title_box {
    flex-direction: row;
  }
}

.title_box h1 {
  text-align: center;
  margin: 20px;
}

.title_box p {
  padding: 0 15%;
}

.title_box img {
  width: 30%;
  margin: 0 auto;
  display: block;
}

